<template>
    <div class="goods-page">
        <div class="back" @touchend="goBack">
            <i class="el-icon-arrow-left"></i>
        </div>
        <div class="goods-intro">
            <img :src="food.image">
            <h3>{{food.name}}</h3>
            <p>月售{{food.sellCount}}份 <span>好评率{{food.rating}}%</span></p>
            <div class="cart-price">
                <div class="price-com">
                    <span class="now-price"><span>￥</span>{{food.price}}</span>
                    <span class="old-price" v-if="food.oldPrice"><span>￥</span>{{food.oldPrice}}</span>
                </div>
                <div class="add-to-cart" @touchend="addToCart" v-if="(foodList.filter(item=>JSON.stringify(item)==JSON.stringify(food)).length) == 0">加入购物车</div>
                <add-minus :food-item="food" v-else></add-minus>
            </div>
        </div>
        <div class="goods-brif" v-if="food.info">
            <h3>商品介绍</h3>
            <p>{{food.info}}</p>
        </div>
        <div class="goods-eva">
            <h3>商品评价</h3>
            <rating-list :ratings="food.ratings" :goods-rating="true"></rating-list>
        </div>
        <elem-footer ></elem-footer>
    </div>

</template>

<script>
import ElemFooter from '@/components/public/ElemFooter'
import RatingList from '@/components/public/RatingList'
import AddMinus from '@/components/public/AddMinus'
import {mapState, mapMutations} from 'vuex'
export default{
    components:{
        ElemFooter,
        RatingList,
        AddMinus
    },
    data(){
        return {
            food:{},
            foodList:[]
        }
    },
    created(){
        this.food = this.$route.params.food;
        this.foodList = [].concat(this.cartList);
    },
    methods:{
       addToCart(){
           this.foodList.push(this.food);
           this.changeCartContent(this.foodList);
       },
       goBack(){
           this.$router.back();
       },
       ...mapMutations(['changeCartContent'])
    },
    computed:{
        ...mapState(['cartList'])
    },
    watch:{
        cartList(val){
            this.foodList = [].concat(this.cartList);
        }
    }
}
</script>

<style scoped lang="scss">
    @import "@/assets/css/goods/goods.scss"
</style>