<template>
    <div class="rating-list">
        <div class="eva-tab">
            <el-row>
                <el-button plain autofocus type="primary" @click.native="tabClick('all')">全部{{ratings.length}}</el-button>
                <el-button plain type="success" @click.native="tabClick('satisfy')">{{goodsRating ? `推荐`:`满意`}}{{satisfy.length}}</el-button>
                <el-button plain type="info" @click.native="tabClick('notSatisfy')">{{goodsRating ? `吐槽`:`不满意`}}{{notSatisfy.length}}</el-button>
            </el-row>
        </div>
        <div @touchend="hasContentClick" :class="{'eva-icon':true,'select':hasContent}">
            <i class="el-icon-success"></i>
            只看有内容的评价
        </div>
        <ul class="eva-list">
            <li class="eva-item" v-for="item in renderRatingList">
                <div class="eva-img">
                    <img :src="item.avatar" alt="">
                </div>
                <div class="eva-info">
                    <div class="avatar-info">
                        <h4 class="avatar">{{item.username}}</h4>
                        <div class="time">{{getDate(item.rateTime).date}} {{getDate(item.rateTime).time}}</div>
                    </div>
                    <p class="rate" v-if="!goodsRating">
                        <el-rate
                            v-model="item.score"
                            disabled
                            text-color="#ff9900"
                            score-template="{value}">
                        </el-rate> 
                        <span class="arrive" v-if="item.deliveryTime">{{item.deliveryTime}}分钟送达</span>
                    </p>
                    <p :class="{'eva-detail':true,'rot':item.rateType == 1}" v-if="goodsRating || item.text">
                        <i class="el-icon-thumb" v-if="goodsRating"></i>
                        <span v-if="item.text">{{item.text}}</span>
                    </p>
                    <div :class="{'rate-tag':true,'rot':item.rateType == 1}" v-if="!goodsRating">
                        <i class="el-icon-thumb" ></i>
                        <span v-for="rec in item.recommend">{{rec}}</span>
                    </div>
                </div>
            </li>
        </ul>
    </div>

</template>

<script>
import { getDate } from '@/util/getDate.js'
export default{
    props:['ratings','goodsRating'],
    data(){
        return{
            getDate,
            notSatisfy:[],
            satisfy:[],
            renderRatingList:[],
            hasContent:false
        }
    },
    created(){
        this.renderRatingList = [].concat(this.ratings);
        this.satisfy = this.ratings.filter(it=>it.rateType == 0);
        this.notSatisfy = this.ratings.filter(it=>it.rateType == 1);
    },
    methods:{
        tabClick(type){
            this.hasContent = false;
            switch(type){
                case 'all':
                    this.renderRatingList = [].concat(this.ratings);
                    break;
                case 'satisfy':
                    this.renderRatingList = [].concat(this.satisfy);
                    break;
                case 'notSatisfy':
                     this.renderRatingList = [].concat(this.notSatisfy);
                    break;
                default:
                    break;
            }
        },
        hasContentClick(){
            this.hasContent = !this.hasContent;
            if(this.hasContent){
                this.renderRatingList = this.ratings.filter(it=>it.text);
            }else{
                this.renderRatingList = [].concat(this.ratings);
            } 
        }
    }
}
</script>

<style scoped lang="scss">
    @import "@/assets/css/public/ratingList.scss"
</style>