const checkIsDigits = n =>{
    return ('0'+ n).slice(-2);
}
const getDate = n =>{
    const date = new Date(n );
    const year = date.getFullYear();
    const month = checkIsDigits(date.getMonth() + 1);
    const day = checkIsDigits(date.getDate());
    const hour = checkIsDigits(date.getHours());
    const minute = checkIsDigits(date.getMinutes());
    const second = checkIsDigits(date.getSeconds());
    return {
        date:`${year}-${month}-${day}`,
        time:`${hour}:${minute}:${second}`
    }
}

export { getDate }